import Config from '../../config';
import ctx from '../index';
import { convertByWei, convertByEth, alertSuccess, alertError } from '../../utils';

const { BumperFarm, BUMPER } = Config.CONTRACT_ADDRESS;
// col
const LP_ADDRESS = Config.LP_ADDRESS
// bumper函数
// 获取bumper的数目
export const getBumperAmount = async (address) => {
  const { Erc20Contract } = ctx.data;
  const instance = await Erc20Contract.at(BUMPER);
  let res = await instance.balanceOf(address)
  let amount = convertByWei(res)
  console.log('bumper', amount)
  ctx.data.BumperAmt = amount
  return
}

// 可用的Lp，SwapLp，在swap中拼成了lp，可去stake
export const getSwapLpAmount = async (col, address) => {
  const { Erc20Contract } = ctx.data;
  const instance = await Erc20Contract.at(LP_ADDRESS[col]);
  let res = await instance.balanceOf(address)
  let amount = convertByWei(res)
  console.log('swap lp', col, amount)
  let map = ctx.data.swapLpAmtMap || {}
  map[col] = amount
  ctx.data.swapLpAmtMap = map
  return
}

export const getStakeLpAmount = async (col, address) => {
  const { BumperFarmContract } = ctx.data;
  const instance = await BumperFarmContract.at(BumperFarm);
  let res = await instance.getUser(LP_ADDRESS[col], address)
  let stakeLp = res[0][0]
  let reward = res[1].length ? res[1][0] : '0'
  stakeLp = convertByWei(stakeLp)
  reward = convertByWei(reward)
  console.log('stakeLp', stakeLp, 'reward', reward)
  let map = ctx.data.stakeLpAmtMap || {}
  map[col] = stakeLp
  ctx.data.stakeLpAmtMap = Object.assign({}, map)
  let map2 = ctx.data.rewardMap || {}
  map2[col] = reward
  ctx.data.rewardMap = Object.assign({}, map2)
  return
}

// 获取总的lp staked的量
export const getLpTotalStaked = async (col) => {
  const { Erc20Contract } = ctx.data;
  const instance = await Erc20Contract.at(LP_ADDRESS[col]);
  let res = await instance.totalSupply()
  let amount = convertByWei(res)
  console.log('lp totalSupply', amount)
  let map = ctx.data.poolStakedMap || {}
  map[col] = amount
  ctx.data.poolStakedMap = map
  return
}

// 获取总的lp中两个token的量
export const getLpTotalReserves = async (col) => {
  const { LpContract, poolSupplyMap = {} } = ctx.data;
  const instance = await LpContract.at(LP_ADDRESS[col]);
  let res = await instance.getReserves()
  let amount0 = convertByWei(res._reserve0) // _reserve0, bctoken
  let amount1 = convertByWei(res._reserve1) // _reserve1, wbnb
  poolSupplyMap[col] = [(0.1 * amount0).toFixed(5), (0.1 * amount1).toFixed(5)]
  ctx.data.poolSupplyMap = poolSupplyMap
  console.log('lp reserve', amount1, ctx.data.poolSupplyMap)
  return
}

export const isApproveLP = async () => {
  const { Erc20Contract, chainAccount } = ctx.data;
  let map = ctx.data.approveLpMap || {}
  for (let col of Object.keys(LP_ADDRESS)) {
    map[col] = {}
    const lpIns = await Erc20Contract.at(LP_ADDRESS[col]);
    const res = await lpIns.allowance(chainAccount, BumperFarm);
    let approveNum = convertByWei(res)
    map[col]['lpStatus'] = approveNum > 0
    map[col]['lpApproved'] = approveNum
  }
  console.log('approveLpMap', map)
  ctx.data.approveLpMap = map
};

// 授权lp
export const approveLP = async (col, amount) => {
  const { Erc20Contract, chainAccount } = ctx.data;
  const lpIns = await Erc20Contract.at(LP_ADDRESS[col]);
  // 授权
  try {
    let res = await lpIns.approve(
      BumperFarm,
      convertByEth(amount),
      {
        from: chainAccount
      }
    );
    return res;
  } catch (err) {
    console.log(err);
  }
};

// stake
export const stake = async (col, num) => {
  ctx.event.emit('showLoading', 'STAKING');
  const amount = convertByEth(num)
  const { BumperFarmContract, chainAccount } = ctx.data;
  const farmIns = await BumperFarmContract.at(BumperFarm)
  try {
    const params = [
      LP_ADDRESS[col],
      amount,
    ]
    let res = await farmIns.deposit(
      ...params,
      {
        from: chainAccount
      }
    );
    alertSuccess('STAKE SUCCESS');
    return res;
  } catch (err) {
    alertError(err.message)
  } finally {
    ctx.event.emit('hideLoading');
  }
}

// withdraw
export const withdraw = async (col, num) => {
  ctx.event.emit('showLoading', 'WITHDRAWING');
  const amount = convertByEth(num)
  const { BumperFarmContract, chainAccount } = ctx.data;
  const farmIns = await BumperFarmContract.at(BumperFarm)
  try {
    const params = [
      LP_ADDRESS[col],
      amount,
    ]
    let res = await farmIns.withdraw(
      ...params,
      {
        from: chainAccount
      }
    );
    alertSuccess('WITHDRAWING SUCCESS');
    return res;
  } catch (err) {
    alertError(err.message)
  } finally {
    ctx.event.emit('hideLoading');
  }
}

// claim
export const claim = async (col) => {
  ctx.event.emit('showLoading', 'CLAIMING');
  const { BumperFarmContract, chainAccount } = ctx.data;
  const farmIns = await BumperFarmContract.at(BumperFarm)
  try {
    let res = await farmIns.claimRewardsForPools(
      [LP_ADDRESS[col]],
      {
        from: chainAccount
      }
    );
    alertSuccess('CLAIMING SUCCESS');
    return res;
  } catch (err) {
    alertError(err.message)
  } finally {
    ctx.event.emit('hideLoading');
  }
}