ctx.event.listen('repay', async (params) => {
  const { num, col, ext } = params;
  let { approveMap = {}, chainAccount } = ctx.data
  // 先approve
  if (approveMap['WBNB']['coreStatus'] !== true || approveMap['WBNB']['coreApproved'] < num) {
    ctx.event.emit('showLoading', 'APPROVING');
    const result = await approvePaired(num, BumperCore);
    approveMap['WBNB']['coreStatus'] = true;
    approveMap['WBNB']['coreApproved'] = num;
    ctx.data.approveMap = approveMap;
    if (!(result && result.receipt.status)) {
      return;
    }
    ctx.event.emit('hideLoading');
  } else {
    const result = await repay(col, num, ext)
    console.log('repay', result)
    updateAccount([chainAccount]);
  }
})

ctx.event.listen('redeem', async (params) => {
  const { num, col, ext } = params;
  const result = await redeem(col, num, ext)
  console.log('redeem', result)
  updateAccount([ctx.data.chainAccount]);
})

ctx.event.listen('collect', async (params) => {
  const { num, col, ext } = params;
  const result = await collect(col, num, ext)
  console.log('collect', result)
  updateAccount([ctx.data.chainAccount]);
})

// farm 操作
ctx.event.listen('stake', async (params) => {
  const { col, num } = params;
  let { approveLpMap = {}, chainAccount } = ctx.data
  // 先approve
  if (approveLpMap[col]['lpStatus'] !== true) {
    ctx.event.emit('showLoading', 'APPROVING ON STAKING');
    const result = await approveLP(col, num);
    approveLpMap[col]['lpStatus'] = true
    ctx.data.approveLpMap = approveLpMap;
    if (!(result && result.receipt.status)) {
      return;
    }
    ctx.event.emit('hideLoading');
  } else {
    const result = await stake(col, num);
    console.log('stake', result);
    updateAccountFarm([chainAccount]);
  }
});

ctx.event.listen('withdraw', async (params) => {
  const { col, num } = params;
  const result = await withdraw(col, num)
  console.log('withdraw', result)
  updateAccountFarm([ctx.data.chainAccount]);
});

ctx.event.listen('claim', async (params) => {
  const { col } = params;
  const result = await claim(col)
  console.log('claim', result)
  updateAccountFarm([ctx.data.chainAccount]);
});
