import Config from '../../config';
import ctx from '../index';

const { BTCB2WBNB, BTCB_BC2WBNB, ETH_BC2WBNB, WBNB2BUMPER, WBNB2ETH } = Config.PRICE_ADDRESS;

// btcb
export const getBtcbPrice = async () => {
  const { LpContract } = ctx.data;
  const instance = await LpContract.at(BTCB2WBNB);
  let res = await instance.getReserves() // token0是btcb，token1是wbnb，所以token1Amt/token0Amt就是price
  ctx.data.btcbPrice = Math.round(res._reserve1 * 100 / res._reserve0) / 100
  console.log('btcbPrice', ctx.data.btcbPrice)
  return
}

export const getBcTokenPrice = async (col) => {
  const address = col === 'BTCB' ? BTCB_BC2WBNB : ETH_BC2WBNB
  const { LpContract } = ctx.data;
  const instance = await LpContract.at(address);
  let res = await instance.getReserves() // token0是bc，token1是wbnb，所以token0Amt/token1Amt就是price
  let price = Math.round(res._reserve0 * 100 / res._reserve1) / 100
  let map = ctx.data.bcPriceMap || {}
  map[col] = price
  ctx.data.bcPriceMap = map
  console.log(col, 'bcPrice', price)
  return
}

