import React, { useEffect, useState, Suspense } from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'
import './events/ethereum';
import ctx, { mapData, unmapActions } from './events';
import { getDefaultTheme } from './utils'

const Header = React.lazy(() => import('./components/Header'));
const Footer = React.lazy(() => import('./components/Footer'));

const Markets = React.lazy(() => import('./pages/Markets'));
const Farms = React.lazy(() => import('./pages/Farms'));
// const Xbumper = React.lazy(() => import('./pages/Xbumper'));

const Home = React.lazy(() => import('./pages/Home'));
const Loading = React.lazy(() => import('./components/Loading'));

const App = () => {
  let defaultTheme = getDefaultTheme();
  const [theme, setTheme] = useState(defaultTheme);

  useEffect(() => {
    // 初始化区块链库
    ctx.event.emit('initEthereum');
    // 获取theme
    const lifetimeObj = {};
    mapData({
      theme(value){
        setTheme(value)
      }
    }, ctx, lifetimeObj);
    return () => {
      unmapActions(lifetimeObj);
    };
  }, []);


  return (
    <div className={`page-theme theme-${theme}`}>
      <Router>
        <Header />
        <div className="page-body">
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route path="/markets">
              <Markets />
            </Route>
            <Route path="/farms">
              <Farms />
            </Route>
            {/* <Route path="/xbumper">
            <Xbumper />
          </Route> */}
          </Switch>
          <Footer />
        </div>
        <Loading />
      </Router>
    </div>
  );
};


const Empty = () => {
  return (
    <>
    </>
  )
}

const renderLoading = () => (<Empty />);

const renderApp = () => (<App />);

const RoutePage = () => (
  <Suspense fallback={renderLoading()}>
    { renderApp()}
  </Suspense>
);

export default RoutePage;
